<template>
	<div class="rank-list">
		<div class="login-header">
			<div @click="routerGo" class="back">
				<i></i>
				<span>返回</span>
			</div>
			
			<h1>{{this.$route.params.name}}</h1>
			
			<router-link to="/">
				<span class="login-home">
					<i></i>
				</span>
			</router-link>
		</div>
		
		
		<div class="rank-active">
			<span v-for="(item,list) in item" :key="list"
				:class="{active:activeCss == list}" 
				@click="getItem(list)">
				{{item}}
			</span>
		</div>
		
		
		<div v-if="content" class="showmore">没有更多书籍</div>
		
		<div v-else class="inner-clear" v-for="(i,indexs) in ranklist.slice(0,20)" :key="indexs">
			<router-link :to="/bookdeta/ + i._id">
				<div class="inner-cover">
					<img :src="[link + i.cover]" class="auto-img">
				</div>
				<div class="inner-right" >
					<h4>{{i.title}}</h4>
					<p>{{i.author}}</p>
					<p>{{i.shortIntro}}</p>
					<p><span>{{i.latelyFollower}}万</span>人气
					<i>|</i>
					<span>{{i.retentionRatio}}%</span>读者留存
					</p>
				</div>
			</router-link>
		</div>
		
		
	</div>
</template>

<script>
	
	export default {
		name:"RankList",
		data(){
			return{
				activeCss:0,
				ranklist:[],
				content:false,
				urls: `${this.$route.params.id}`,
				link:'http://statics.zhuishushenqi.com',
				item:[
					'周榜',
					'月榜',
					'总榜',
				],
				monthRank:[],
				idUrl:[],
				totalRank:[],
			}
		},
// 		beforeRouteEnter(to,from,next){
// 			next(vm => {
// 				vm.axios.get(vm.Ranklist).then(r => {
// 					vm.monthRank = r.data.ranking.monthRank
// 					vm.idUrl = r.data.ranking._id
// 					vm.totalRank = r.data.ranking.totalRank
// 					vm.ranklist = r.data.ranking.books
// 					console.log(vm.urls)
// 				})
// 			})
// 		},
// 				


		created(){
			this.axios.get(this.Ranklist).then(r => {
				this.monthRank = r.data.ranking.monthRank
 				this.idUrl = r.data.ranking._id
				this.totalRank = r.data.ranking.totalRank
				this.ranklist = r.data.ranking.books.slice(0,20)
			})
		},
		
		watch:{
			urls(news,old){
				if(this.urls == null){
					this.content = true
				}else{
					this.content = false
					this.axios.get(this.Ranklist).then(r => {
						this.ranklist = r.data.ranking.books
					})
				}
			}
		},
		
		methods:{
			routerGo(){
				this.$router.go(-1)
				this.$destroy(true)
			},
			
			getItem(list){
				this.activeCss = list
				
				if(list == 1) {
					this.urls = this.totalRank
				}
				
				if(list == 2) {
					this.urls = this.monthRank
				}
				
				if(list == 0) {
					this.urls = this.idUrl
				}
				
			},
			
		},
		computed:{
			Ranklist(){
				let ranks = 'api/ranking/' + this.urls
				return ranks
			}
		}
	}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
	@import "../../public/sass/mixin";
	.rank-list{
		background: #f5f2ed;
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
	}
	
	.login-header{
		width: 100%;
		height: 2.75rem;
		background-color: #b93321;
		color: #fff;
		display: block;
	
		.back{
			display: inline-block;
			text-align: center;
			height: 100%;
			width: 60px;
			float: left;
			
			i{
				display: inline-block;
				background: url(../../public/img/arror-back.png) no-repeat center;
				background-size: 125%;
				height: 100%;
				width: 0.9375rem;
				vertical-align: middle;
			}
			span{
				vertical-align: middle;
				font-size: 12px;
				margin-left: 2px;
				color:#fff;
			}
		}
		
		h1{
			width: 70%;
			display: inline-block;
			text-align: center;
			line-height: 2.75rem;
		}
		
		.login-home{
			background: url(../../public/img/home.png) no-repeat center;
			background-size: 35%;
			height: 100%;
			width: 14%;
			float: right;
			display: inline-block;
		}
		
	}
	
	.inner-clear{
		width: 100%;
		height: 6.5625rem;
		padding: 0.9375rem;
		background-color: #F5F2ED;
		
		
		
		.inner-cover{
			float: left;
			width: 3.625rem;
			height: 100%;
			margin-right: 0.9375rem;
		}
		
		.inner-right{
			float: left;
			width: 75%;
			
			h4{
				font-size: 16px;
				font-weight: 700;
				color: #333;
			}
			p{
				-webkit-line-clamp: 1;
				white-space: nowrap;
				text-overflow: ellipsis;
				margin-top: 0.5rem;
				overflow: hidden;
				font-size: 12px;
				color: #999;
			}
			i{
				margin: 0 0.625rem;
			}
			span{
				color: #b93321;
			}
		}
	}

	.rank-active{
		display: flex;
		background: #fff;
		
		span{
			flex: 1;
			height: 3.375rem;
			text-align: center;
			font-size: 0.875rem;
			line-height: 3.375rem;
			color: #999;
		}
		.active{
			border-bottom: 2px solid #b93321;
		}
	}

	.showmore{
		text-align: center;
		height: 2.5rem;
		line-height: 2.5rem;
		color: #999;
		font-size: 0.75rem;
	}
</style>
